<template>
  <div class="drive">
    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Request Time</th>
          <th scope="col">Customer' Name</th>
          <th scope="col">Assistant Service Manager</th>
          <th scope="col">Model</th>
          <th scope="col">Color</th>
          <th scope="col">Reason</th>
          <th scope="col">Complete Time</th>
          <th scope="col">Δ Time</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(req, index) in reqs" :key="index">
          <th scope="row">{{index + 1}}</th>
          <td>{{ dateFormatReq(req.request_timestamp)}}</td>
          <td>{{ req.customer_name }}</td>
          <td>{{ req.associate }}</td>
          <td>{{ req.model }}</td>
          <td>{{ req.color }}</td>
          <td>{{ req.reason }}</td>
          <td>{{ dateFormatReq(req.completion_timestamp) }}</td>
          <td>{{ total(req.request_timestamp, req.completion_timestamp) }} min.</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { db } from "../main";
import { setInterval } from "timers";
import router from "../router";

export default {
  name: "Drive",
  data() {
    return {
      reqs: [],
      currentTime: Date.now(),
      state: "started"
    };
  },
  firestore() {
    return {
      reqs: db.collection("drive").where("status", "==", "Complete")
    };
  },
  methods: {
    updateCurrentTime: function() {
      if (this.$data.state == "started") {
        this.currentTime = Date.now();
      }
    },
    elapsedTime(req) {
      const timestamp = req.request_timestamp;
      const startTime = timestamp.seconds * 1000;
      return Math.floor((this.currentTime - startTime) / 60000);
    },
    dateFormatReq(date) {
      return new Date(date.seconds * 1000).toLocaleString();
    },
    total(req, complete) {
      return Math.floor((complete.seconds - req.seconds) / 60);
    }
  },
  mounted: function() {
    this.interval = setInterval(this.updateCurrentTime, 1000);
  },
  destroyed: function() {
    clearInterval(this.interval);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



