<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-12 heading">
        <h1 class="display-3">Drive Status</h1>
      </div>
      <form
        @submit.prevent="newReq(tag_number, associate, model, color, reason, waiter, courtesy_wash, waiting_for_RO, loan_car_checkin, customer_name)"
        class="col-sm-12"
      >
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="tag_number">Tag #</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="tag_number"
              placeholder="Service tag number..."
              v-model="tag_number"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="associate">Associate Name</label>
            <select id="associate" class="form-control form-control-lg" v-model="associate">
              <option v-for="(ass, index) in associates" :key="index">{{ ass }}</option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-12">
            <label for="customer_name">Customer Name</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="customer_name"
              placeholder="Please enter the customers name here..."
              v-model="customer_name"
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="model">Model</label>
            <input
              type="text"
              class="form-control form-control-lg"
              id="model"
              placeholder="ex: 3 Series, 335i"
              v-model="model"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="color">Color</label>
            <input type="text" class="form-control form-control-lg" id="color" v-model="color" />
          </div>
        </div>
        <div class="form-group">
          <!-- <label for="reason">Reason</label>
          <div class="form-check">
            <input
              class="form-check-input position-static"
              type="checkbox"
              id="waiter"
              value="option1"
              aria-label="..."
              v-model="waiter"
            />
          <label for="waiter">Waiter</label>-->
          <!-- <div class="form-check">
            <input
              class="form-check-input position-static"
              type="checkbox"
              id="courtesy_wash"
              value="option1"
              aria-label
              v-model="courtesy_wash"
            />
            <label for="courtesy_wash">Courtesy Wash</label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input position-static"
              type="checkbox"
              id="waiting_for_RO"
              value="option1"
              aria-label
              v-model="waiting_for_RO"
            />
            <label for="waiting_for_RO">Waiting for RO to be Written</label>
          </div>
          <div class="form-check">
            <input
              class="form-check-input position-static"
              type="checkbox"
              id="loan_car_checkin"
              value="option1"
              aria-label
              v-model="loan_car_checkin"
            />
            <label for="loan_car_checkin">Loan Car Check-In</label>
          </div>-->
          <select id="associate" class="form-control form-control-lg" v-model="reason">
            <option v-for="(reason, index) in reasons" :key="index">{{ reason }}</option>
          </select>
        </div>
        <button type="submit" class="btn btn-primary">Submit</button>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Request",
  data() {
    return {
      reqs: [],
      reqTimestamp: new Date(),
      tag_number: "",
      associate: "",
      model: "",
      color: "",
      reason: "",
      waiter: "",
      customer_name: "",
      courtesy_wash: "",
      waiting_for_RO: "",
      loan_car_checkin: "",
      reasons: ["Waiting on RO", "Car Wash", "Return Loan Car"],
      associates: [
        "Aaron Calvert",
        "Alamd Rosario",
        "Albert Saenz",
        "Allie Peters",
        "Anthony Baerga",
        "Antonio Martinez",
        "Dale Fischbach",
        "Emmanuel Colon",
        "Faylin Miller",
        "James Shiflett",
        "Joseph Pantoja",
        "Juan Cantu",
        "Lou Gatti",
        "Luis Cotto",
        "Maggie King",
        "Marissa Munoz",
        "Mohammad Dmour",
        "Rhaudell Cordova",
        "Robert Gomez",
        "Ryan Mararac",
        "Ryan Watson",
        "Skyler Schnee",
        "Valet",
        "Transportation"
      ]
    };
  },
  firestore() {
    return {
      reqs: db.collection("drive")
    };
  },
  methods: {
    newReq(
      tag,
      associate,
      model,
      color,
      reason,
      waiter,
      courtesy_wash,
      waiting_for_RO,
      loan_car_checkin,
      customer_name
    ) {
      db.collection("drive").add({
        tag_number: tag,
        associate: associate,
        model: model,
        color: color,
        reason: reason,
        status: "Incomplete",
        request_timestamp: new Date(),
        customer_name: customer_name,
        waiter: waiter,
        courtesy_wash: courtesy_wash,
        waiting_for_RO: waiting_for_RO,
        loan_car_checkin: loan_car_checkin
      });
      this.tag_number = "";
      this.associate = "";
      this.model = "";
      this.color = "";
      this.reason = "";
      this.$router.push("/driveemployee");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.heading {
  text-align: center;
  margin-top: 40px;
  margin-bottom: 40px;
}
</style>


