import Vue from "vue";
import Router from "vue-router";
import Drive from "@/components/Drive";
import Complete from "@/components/Complete";
import Request from "@/components/Request";
import DriveEmployee from "@/components/DriveEmployee";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Drive",
      component: Drive
    },
    {
      path: "/complete",
      name: "Complete",
      component: Complete
    },
    {
      path: "/request",
      name: "Request",
      component: Request
    },
    {
      path: "/driveemployee",
      name: "DriveEmployee",
      component: DriveEmployee
    }
  ]
});



