<template>
  <div class="drive">
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Timer</th>
          <th scope="col">Customer' Name</th>
          <th scope="col">Associate Name</th>
          <th scope="col">Model</th>
          <th scope="col">Color</th>
          <th scope="col">Reason</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(req, index) in reqs" :key="index">
          <td scope="row">{{index + 1}}</td>
          <td
            class="badge badge-secondary startTime"
            v-if="elapsedTime(req) <= 15"
          >{{ elapsedTime(req) }} min.</td>
          <td
            class="badge badge-secondary timer"
            v-else-if="elapsedTime(req) <= 20"
          >{{ elapsedTime(req) }} min.</td>
          <td
            class="badge badge-secondary timerr"
            v-else-if="elapsedTime(req) <= 25"
          >{{ elapsedTime(req) }} min.</td>
          <td
            class="badge badge-secondary timerrr animated infinite flash slow"
            v-else
          >{{ elapsedTime(req) }} min.</td>
          <td>{{ req.customer_name }}</td>
          <td>{{ req.associate }}</td>
          <td>{{ req.model }}</td>
          <td>{{ req.color }}</td>
          <td>{{ req.reason }}</td>
          <td>
            <button @click="complete(req.id)" class="btn btn-outline-success">Complete</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { db } from "../main";
import { setInterval } from "timers";
import router from "../router";

export default {
  name: "Drive",
  data() {
    return {
      reqs: [],
      currentTime: Date.now(),
      state: "started"
    };
  },
  firestore() {
    return {
      reqs: db.collection("drive").where("status", "==", "Incomplete")
    };
  },
  methods: {
    updateCurrentTime: function() {
      if (this.$data.state == "started") {
        this.currentTime = Date.now();
      }
    },
    elapsedTime(req) {
      const timestamp = req.request_timestamp;
      const startTime = timestamp.seconds * 1000;
      return Math.floor((this.currentTime - startTime) / 60000);
    },
    complete(id) {
      db.collection("drive")
        .doc(id)
        .update({ completion_timestamp: new Date(), status: "Complete" });
    }
  },
  mounted: function() {
    this.interval = setInterval(this.updateCurrentTime, 1000);
  },
  destroyed: function() {
    clearInterval(this.interval);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.timer {
  color: #bf9000;
  background-color: #fff2cc;
}

.timerr {
  color: #990000;
  background-color: #f4cccc;
}

.timerrr {
  color: #990000;
  background-color: #f4cccc;
}

.startTime {
  color: #274e13;
  background-color: #d9ead3;
}

.active {
  background-color: red;
}

tr {
  font-size: 1.2rem;
  text-align: center;
  height: 70px;
}
th {
  font-size: 40px;
}

td {
  font-size: 30px;
  display: table-cell;
  vertical-align: middle;
}
</style>




